import {useHttp} from '../hooks/http.hook';

const useMarvelService = () => {

    const {loading, request, error, clearError} = useHttp();

    const _apiBase = 'https://gateway.marvel.com:443/v1/public/';
    const _apiKey = 'apikey=00ae2959aaae5275a6828832c52b7182';
    const _baseOffset = 210; //это базовая величина отступа от начала списка персонажей

    
    //Функция заменена на хук useHttp 
    // getResource = async (url) => {
    //     let res = await fetch(url);

    //     if(!res.ok) {
    //         throw new Error(`Could not fetch ${url}, status: ${res.status}`);
    //     }

    //     return await res.json();
    // }

    // getAllCharacters = () => {
    //         return this.getResource('https://gateway.marvel.com:443/v1/public/characters?limit=9&offset=210&apikey=00ae2959aaae5275a6828832c52b7182');
    //     }
    
    // getCharacter = (id) => {
    //         return this.getResource(`https://gateway.marvel.com:443/v1/public/characters/${id}?apikey=00ae2959aaae5275a6828832c52b7182`);
    //     }

    const getCharacterByName = async (name) => {
        const result = await request(`${_apiBase}characters?name=${name}&${_apiKey}`);
        return result.data.results.map(_transformCharacter);
    }
    
    const getAllCharacters = async (offset = _baseOffset) => {//передаем аргумент по умолчанию, если аргумент передан, то он подставляется, если не передан, то подставляется _baseOffset
        const res = await request(`${_apiBase}characters?limit=9&offset=${offset}&${_apiKey}`);
        return res.data.results.map(_transformCharacter); //В мар подставляется колбэк-функция, к-я будет вызываться
        //для каждого элемента массива results.
        //data.results - это объекты из данных, которые приходят с сервера
    }

    const getCharacter = async (id) => {
        //request - асинхронная функция, мы не знаем сколько времени будет идти ответ от сервера
        //поэтому ставим async - await, чтобы дождаться выполнения функции request и записать значение в переменную res
        const res = await request(`${_apiBase}characters/${id}?${_apiKey}`);
        return _transformCharacter(res.data.results[0]);
    }


    //метод для трансформации данных, полученных от сервера. Получаем от сервера res, а возвращаем только то, что нам нужно
    //свойства зависят от вида данных
    const _transformCharacter = (character) => {
        return {
            id: character.id,
            name: character.name,
            description: character.description ? `${character.description.slice(0, 210)}...` : 'There is no description for this character',
            thumbnail: character.thumbnail.path + '.' + character.thumbnail.extension,
            homepage: character.urls[0].url,
            wiki: character.urls[1].url,
            comics: character.comics.items
        }
    }

    const getComicsList = async (offset = 0) => {
        const response = await request(`${_apiBase}comics?&limit=8&offset=${offset}&${_apiKey}`);

        // https://gateway.marvel.com:443/v1/public/comics?limit=8&offset=0&apikey=00ae2959aaae5275a6828832c52b7182
        return response.data.results.map(_transformComics);
    }

    const getComic = async (id) => {
        const response = await request(`${_apiBase}comics/${id}?${_apiKey}`);
        return _transformComics(response.data.results[0]);
    }

    const _transformComics = (comics) => {
        return {
            id: comics.id,
            title: comics.title,
            description: comics.description || 'There is no description',
            pageCount: comics.pageCount ? `${comics.pageCount} p.` : 'No information about the number of pages',
            thumbnail: comics.thumbnail.path + '.' + comics.thumbnail.extension,
            language: comics.textObjects.language || 'en-us',
            price: comics.prices[0].price ? `${comics.prices[0].price}$` : `n/a`
        }
    }

    return {loading, error, getAllCharacters, getCharacter, clearError, getComicsList, getComic, getCharacterByName } 
}

export default useMarvelService;