import { useState, useCallback } from "react";


//пользовательских хук, будет реализовывать функционал, повторяющийся в разных компонентах
//изменение состояния - загрузка, ошибка(при запросе к серверу)
//запрос на сервер
export const useHttp = () => {
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    //используем useCallback, т.к. функцию запросов на сервер будем в том числе передавать в дочерние компоненты и это
    //поможет избежать их лишней перерисовки
    const request = useCallback(async (url, method = 'GET', body = null, headers = {'Content-Type': 'application/json'}) => 
        {
        setLoading(true);

        try {
            const response = await fetch(url, {method, body, headers});

            if(!response.ok) {
                throw new Error(`Could not fetch ${url}, status: ${response.status}`);
            }

            const data = await response.json();
            setLoading(false); //загрузка завершилась, состояние изменено на false
            return data; //функция возвращает данные, полученные с сервера
        
        //блок для обработки ошибки при запросе к серверу
        } catch(e) {
            setLoading(false); //загрузка завершилась (даже если и с ошибкой)
            setError(e.message);//в состояние запишем сообщение об ошибке
            throw e;
        } 

        }, [])

    //функция для обнуления сообщения об ошибке
    const clearError = useCallback(() => setError(null), []);

    return {loading, request, error, clearError}
}
